.no-margin {
  margin: 0;
}

/* USER-MENU (DROP-MENU) */
.user-menu {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: none;
  background-blend-mode: overlay;
  border-radius: 1rem;
  position: absolute;
  top: 15rem;
  transition: all 150ms ease-in-out;
  max-height: 0;
  opacity: 0;
  overflow: hidden;
}
@media (min-width: 469px) {
  .user-menu {
    right: 2rem;
    top: 8rem;
    bottom: 50rem;
  }
}
.user-menu .user-menu__button--profile {
  color: #FAEBEF;
  background-color: hsl(238, 40%, 52%);
  width: 100%;
  margin: 0;
  padding: 0.5rem 0.8rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
}
.user-menu .user-menu__button--profile:hover {
  transition: all 150ms;
  background-color: #777ac5;
  cursor: pointer;
}
.user-menu .user-menu__button--settings {
  color: #FAEBEF;
  background-color: hsl(211, 10%, 45%);
  width: 100%;
  margin: 0;
  padding: 0.5rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
}
.user-menu .user-menu__button--settings:hover {
  transition: all 150ms;
  background-color: #818c98;
  cursor: pointer;
}
.user-menu .user-menu__button--logout {
  color: #FAEBEF;
  background-color: hsl(358, 100%, 59%);
  width: 100%;
  margin: 0;
  padding: 0.5rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
}
.user-menu .user-menu__button--logout:hover {
  transition: all 150ms;
  background-color: #ff6166;
  cursor: pointer;
}
.user-menu .user-menu__button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1rem;
  border-radius: 1rem;
  width: auto;
}
.user-menu .user-menu__button:first-of-type {
  margin: 0;
}
.user-menu.user-menu--showing {
  transition: all 300ms ease-in-out;
  padding: 1rem;
  max-height: 16rem;
  overflow: hidden;
  opacity: 1;
  z-index: 5;
  background-color: rgba(250, 235, 239, 0.3);
  backdrop-filter: blur(10px);
}

.comment-body {
  display: flex;
  align-items: center;
  gap: 2rem;
  grid-area: content-start;
  grid-row: auto;
  margin: 1rem;
  padding: 2rem;
  border-radius: 1rem;
  background-color: hsl(0, 0%, 100%);
  color: hsl(211, 10%, 45%);
}

.votes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 11rem;
  padding: 1rem;
  border-radius: 1rem;
  background-color: hsl(223, 19%, 93%);
}
.votes .vote-num {
  color: hsl(238, 40%, 52%);
  font-size: 2rem;
}

.comment-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  color: hsl(212, 24%, 26%);
  font-weight: 700;
}
.comment-info .user-ppf {
  width: 5rem;
  height: 5rem;
}
.comment-info .user-info {
  display: flex;
  gap: 1rem;
  font-weight: bold;
}
.comment-info .user-info .comment-date {
  font-weight: normal;
  color: hsl(211, 10%, 45%);
}

.reply-button {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 1.6rem;
  color: hsl(238, 40%, 52%);
  padding: 1rem;
  border-radius: 0.7rem;
  transition: background-color 200ms;
}
.reply-button img {
  width: 2rem;
}
.reply-button:hover {
  cursor: pointer;
  transition: background-color 200ms;
  background-color: hsl(239, 57%, 85%);
}
.reply-button::after {
  content: "Reply";
  display: inline-block;
}

footer {
  grid-column: break-start/break-end;
  grid-row: auto;
}

.send-mensage {
  display: flex;
  gap: 2rem;
  padding: 2rem;
  margin: 1rem 0 3rem 0;
  overflow: visible;
  border-radius: 1rem;
  background-color: hsl(0, 0%, 100%);
}
.send-mensage textarea {
  flex: 1 0;
  background-color: hsl(0, 0%, 100%);
  border: 2px hsl(223, 19%, 93%) solid;
  border-radius: 0.5rem;
  outline: none;
  resize: none;
  height: 7rem;
  padding: 1rem;
  max-height: auto;
  font-size: 1.6rem;
  color: hsl(211, 10%, 45%);
}
.send-mensage textarea:focus {
  outline: hsl(238, 40%, 52%) solid 1px;
}
.send-mensage .user-ppf {
  width: 3rem;
  height: 3rem;
}
.send-mensage .send-button {
  display: flex;
  align-items: center;
  padding: 2rem;
  min-width: 5rem;
  height: 1rem;
  background-color: hsl(238, 40%, 52%);
  border-radius: 0.7rem;
  color: hsl(0, 0%, 100%);
  font-weight: 500;
  font-size: 1.5rem;
  transition: background-color 200ms;
}
.send-mensage .send-button:hover {
  transition: background-color 200ms;
  background-color: hsl(239, 57%, 85%);
}

:root {
  --flare: url(../../src/images/flare.svg);
}

.landing-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.landing-page .landig-page__title {
  text-align: left;
  font-weight: 500;
  font-size: 4.8rem;
  margin: 5.4rem 0 0 5.4rem;
  height: 30%;
}

.landing-page__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.landing-page__content .landing-page__paragraph {
  width: 50rem;
  font-size: 2.4rem;
}
.landing-page__content .landing-page__link {
  text-transform: capitalize;
  position: relative;
  padding: 1rem;
  font-size: 2.4rem;
  color: #5661C6;
  border-radius: 1rem;
  font-weight: 500;
  background: rgb(250, 235, 239);
  background: linear-gradient(141deg, rgb(250, 235, 239) 0%, rgb(86, 97, 198) 100%);
  background-size: 30rem;
  background-position: right;
  transition: all 300ms;
}
.landing-page__content .landing-page__link::before {
  content: var(--flare);
  position: absolute;
  bottom: 75%;
  right: 95%;
  transform: rotateZ(0deg);
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
.landing-page__content .landing-page__link::after {
  content: "";
  position: absolute;
  bottom: 1rem;
  left: 1.2rem;
  width: 85%;
  height: 0.1em;
  opacity: 0;
  transition: all 300ms;
  background-color: #5661C6;
  transform: scale(0);
  border-radius: 1rem;
}
.landing-page__content .landing-page__link:hover {
  transition: all 300ms;
  background-position: left;
}
.landing-page__content .landing-page__link:hover::before {
  content: var(--flare);
  position: absolute;
  bottom: 75%;
  right: 95%;
  transform: rotateZ(15deg);
  opacity: 1;
  transition: all 0.2s ease-in-out;
}
.landing-page__content .landing-page__link:hover::after {
  opacity: 1;
  transform: scale(1);
}

.login {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(250, 235, 239);
  background: radial-gradient(circle, rgb(250, 235, 239) -150%, rgb(86, 97, 198) 100%);
}

form {
  background-color: #5661C6;
  color: #FAEBEF;
  border-radius: 0.5rem;
  padding: 3rem;
  font-size: 2rem;
  margin: 3rem;
  width: 50rem;
}
form fieldset {
  display: flex;
  flex-direction: column;
  border: none;
  padding: 0;
}
form label,
form input[type=submit] {
  font-weight: 500;
}
form input {
  padding: 1rem;
  outline: none;
  border: none;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  background-color: #FAEBEF;
  color: #5661C6;
  font-weight: 500;
}
form input[type=submit] {
  color: #FAEBEF;
  background-color: hsl(238, 40%, 52%);
  width: 100%;
  margin: 0;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
}
form input[type=submit]:hover {
  transition: all 150ms;
  background-color: #777ac5;
  cursor: pointer;
}
@media (min-width: 469px) {
  form input[type=submit] {
    width: auto;
  }
}
form .errors {
  color: hsl(358, 100%, 59%);
  font-weight: bold;
  text-align: justify;
  margin-bottom: 0.5rem;
}
form .errors:last-of-type {
  margin-bottom: 2rem;
}
form .errors::before {
  content: "⚠️";
}

.create-account-paragraph {
  margin: 1.6rem 0 0 0;
  text-align: center;
}
.create-account-paragraph a {
  color: #FAEBEF;
  font-weight: 500;
}

.topics-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  flex-direction: column;
  position: relative;
}
.topics-nav .welcome-mesaje {
  width: 17.4rem;
}
@media (min-width: 469px) {
  .topics-nav {
    flex-direction: row;
    position: inherit;
  }
  .topics-nav .welcome-mesaje {
    width: auto;
  }
}
.topics-nav .actions {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
}
.topics-nav .actions .user-ppf {
  object-fit: cover;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
}